body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}

.background-video-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.background-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.homepage {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  z-index: 1;
  position: relative;
  padding: 20px;
  background: white;
}

.text-container {
  background-color: rgba(230, 230, 250, 0.5);
  backdrop-filter: blur(30px);
  backdrop-filter: saturate(200%);
  padding: 20px;
  border-radius: 10px;
}
